
*{
    margin: 0;
}
body{
    margin: 40px;
}
.nav1 , .nav2{
    display: flex;
    align-items: center;
}
.image{
    padding: 10px 10px;
    height: 80px;
    width: 80px;
    margin-right:auto;
}
.nav2>h3{
    margin-right: 20px;
}
.image-container {
    padding: 10px;
    margin-left: 40px;
    height: 200px;
    width: 200px;
    margin-right: auto;
    background-image: url('/public/img_2.jpg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover; /* Adjust as needed */
    border-radius: 50%;
    border: 2px solid #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    filter: grayscale(50%);
    /* Add other styles or effects as needed */
}

.content1{
    display: flex;
    align-items: center;
    margin: 60px ;
    margin-left: 50px;
    line-height: 40px;
}
.content2{
    margin: 0;
    list-style-type: none;  
}
span{
    color: brown;
}
h1{
    font-weight: 700;
    font-family: Georgia, 'Times New Roman', Times, serif;
}